"use client";

import { useAuthStore } from "@/store/auth";
import { useTaskStore } from "@/store/task";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { Plus, Edit, Trash } from "lucide-react";

type TaskForm = {
  title: string;
  description: string;
  priority: "LOW" | "MEDIUM" | "HIGH";
  deadline: string;
};

const Dashboard = () => {
  const { user, fetchUser, accessToken, loading, logout } = useAuthStore();
  const { tasks, fetchTasks, deleteTask, createTask, updateTask } =
    useTaskStore();
  const router = useRouter();
  const [initialized, setInitialized] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [form, setForm] = useState<TaskForm>({
    title: "",
    description: "",
    priority: "MEDIUM",
    deadline: "",
  });
  const [editId, setEditId] = useState<string | null>(null);

  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (token) {
      useAuthStore.setState({ accessToken: token });
    }
    setInitialized(true);
  }, []);

  useEffect(() => {
    if (initialized && accessToken && !user) {
      fetchUser();
    }
  }, [initialized, accessToken, user, fetchUser]);

  useEffect(() => {
    if (initialized && !loading && !accessToken) {
      router.push("/login");
    }
  }, [initialized, loading, accessToken, router]);

  useEffect(() => {
    fetchTasks();
  }, [fetchTasks]);

  if (!initialized || loading || (!user && accessToken)) {
    return (
      <div className="flex h-screen w-full items-center justify-center">
        <p className="text-lg font-medium">Loading...</p>
      </div>
    );
  }

  if (!user) return null;

  const handleSubmit = async () => {
    if (editId) {
      await updateTask(editId, form);
    } else {
      await createTask(form);
    }
    setForm({ title: "", description: "", priority: "MEDIUM", deadline: "" });
    setEditId(null);
    setShowForm(false);
  };

  return (
    <div className="p-6">
      <header className="flex items-center justify-between mb-6">
        <h1 className="text-2xl font-bold">Task Management</h1>
        <button
          onClick={() => setShowForm(true)}
          className="flex items-center gap-2 px-4 py-2 bg-[#6858D5] text-white rounded-lg shadow hover:bg-[#4B3BAA] transition"
        >
          <Plus size={18} /> Add Task
        </button>
      </header>

      {/* Table */}
      <div className="bg-white rounded-xl shadow overflow-hidden">
        <table className="w-full text-sm">
          <thead className="bg-gray-50 text-left">
            <tr>
              <th className="px-6 py-3 font-medium text-gray-600">Title</th>
              <th className="px-6 py-3 font-medium text-gray-600">Priority</th>
              <th className="px-6 py-3 font-medium text-gray-600">Deadline</th>
              <th className="px-6 py-3 font-medium text-gray-600">Actions</th>
            </tr>
          </thead>
          <tbody>
            {tasks.map((task) => (
              <motion.tr
                key={task._id}
                initial={{ opacity: 0, y: 8 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.2 }}
                className="border-t"
              >
                <td className="px-6 py-4">{task.title}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-2 py-1 text-xs rounded-lg ${
                      task.priority === "HIGH"
                        ? "bg-red-100 text-red-700"
                        : task.priority === "MEDIUM"
                        ? "bg-yellow-100 text-yellow-700"
                        : "bg-green-100 text-green-700"
                    }`}
                  >
                    {task.priority}
                  </span>
                </td>
                <td className="px-6 py-4">
                  {new Date(task.deadline).toLocaleDateString()}
                </td>
                <td className="px-6 py-4 flex gap-3">
                  <button
                    onClick={() => {
                      setForm(task);
                      setEditId(task._id);
                      setShowForm(true);
                    }}
                    className="text-blue-600 hover:underline flex items-center gap-1"
                  >
                    <Edit size={14} /> Edit
                  </button>
                  <button
                    onClick={() => deleteTask(task._id)}
                    className="text-red-600 hover:underline flex items-center gap-1"
                  >
                    <Trash size={14} /> Delete
                  </button>
                </td>
              </motion.tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Add/Edit Form */}
      {showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center">
          <div className="bg-white rounded-xl p-6 shadow-xl w-96">
            <h2 className="text-lg font-semibold mb-4">
              {editId ? "Edit Task" : "Add Task"}
            </h2>
            <input
              type="text"
              placeholder="Title"
              value={form.title}
              onChange={(e) => setForm({ ...form, title: e.target.value })}
              className="w-full mb-3 px-3 py-2 border rounded"
            />
            <textarea
              placeholder="Description"
              value={form.description}
              onChange={(e) =>
                setForm({ ...form, description: e.target.value })
              }
              className="w-full mb-3 px-3 py-2 border rounded"
            />
            <select
              value={form.priority}
              onChange={(e) =>
                setForm({
                  ...form,
                  priority: e.target.value as "LOW" | "MEDIUM" | "HIGH",
                })
              }
              className="w-full mb-3 px-3 py-2 border rounded"
            >
              <option value="low">Low</option>
              <option value="medium">Medium</option>
              <option value="high">High</option>
            </select>
            <input
              type="date"
              value={form.deadline}
              onChange={(e) => setForm({ ...form, deadline: e.target.value })}
              className="w-full mb-3 px-3 py-2 border rounded"
            />
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setShowForm(false)}
                className="px-4 py-2 bg-gray-200 rounded hover:bg-gray-300"
              >
                Cancel
              </button>
              <button
                onClick={handleSubmit}
                className="px-4 py-2 bg-[#6858D5] text-white rounded hover:bg-[#4B3BAA]"
              >
                Save
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Dashboard;
